body {
  background-color: #2f5d75;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: white;
  border-radius: 15px;
  min-height: 100vh;
}

.paymerang_logo {
  margin-top: 5vh;    
  margin-left: auto;
  margin-right: auto;
}

.paymerang-description {
  color: #2f5d75
}

.app-body {
  color: #2f5d75
}

@media only screen and (max-width: 575px) {
  .App {
      border-radius: 0px;
  }
}
.payee_info {
    color:#cb4197;
}

.list-results {
    border: 2px solid #2f5d75;
    margin: 5px;
}

.list-group-heading {
    font-weight: bold;
    text-align: center;
}

.remit_info {
    overflow: scroll;
}
